/* ==================================== */
/* PARTE 2: FUNDO AURORA (GLOBAL)       */
/* ==================================== */
.aurora-background {
  position: relative;
}
.aurora-background::before {
  content: "";
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  width: 100%; height: 100%;
  background-image: radial-gradient(at 20% 30%, hsla(25, 90%, 50%, 0.3) 0px, transparent 50%),
                    radial-gradient(at 80% 20%, hsla(20, 90%, 40%, 0.2) 0px, transparent 50%),
                    radial-gradient(at 70% 80%, hsla(0, 0%, 100%, 0.1) 0px, transparent 50%);
  filter: blur(100px); 
  z-index: -1; 
  animation: pulse-aurora 15s infinite ease-in-out;
}
@keyframes pulse-aurora {
  0%, 100% { opacity: 0.8; transform: scale(1); }
  50% { opacity: 1; transform: scale(1.1); }
}

/* ==================================== */
/* PARTE 3: CARROSSÉIS (CORRIGIDO)      */
/* ==================================== */
.carousel-container {
  overflow: hidden; 
  width: 100%;
}
.carousel-track {
  display: flex; 
  animation: scroll-left 40s linear infinite; 
}
.carousel-track:hover {
  animation-play-state: paused;
}
@keyframes scroll-left {
  0% { transform: translateX(0); }
  100% { transform: translateX(-50%); } 
}
.carousel-item {
  flex-shrink: 0;
  width: 28%;
  padding: 0 16px; /* AUMENTADO O ESPAÇAMENTO ENTRE AS FOTOS */
  aspect-ratio: 16 / 9; 
  overflow: hidden;       
  border-radius: 0.75rem; 
  position: relative;     
}
@media (max-width: 768px) {
  .carousel-item { width: 45%; padding: 0 12px; }
}
@media (max-width: 480px) {
  .carousel-item { width: 80%; padding: 0 8px; }
}

.logo-carousel-track {
  display: flex;
  animation: scroll-logos 60s linear infinite;
}
.logo-carousel-track:hover {
  animation-play-state: paused;
}
@keyframes scroll-logos {
  0% { transform: translateX(0); }
  100% { transform: translateX(-50%); } 
}
.logo-carousel-item {
  flex-shrink: 0;
  width: 16.66%;
  padding: 0 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1024px) {
  .logo-carousel-item { width: 25%; }
}
@media (max-width: 640px) {
  .logo-carousel-item { width: 33.33%; }
}

body::-webkit-scrollbar { display: none; }
body { -ms-overflow-style: none; scrollbar-width: none; }
.hide-scrollbar::-webkit-scrollbar { display: none; }
.hide-scrollbar { -ms-overflow-style: none; scrollbar-width: none; }

/* ==================================== */
/* PARTE 4: EFEITOS DA HOME (ATUALIZADOS) */
/* ==================================== */

/* --- CORREÇÃO DE Z-INDEX --- */
/* A fumaça (nuvens) deve ficar atrás do conteúdo, mas na frente das luzes */
.smoke-effect {
  position: absolute;
  top: 0; left: 0; width: 100%; height: 100%;
  pointer-events: none; 
  overflow: hidden; 
  z-index: 2; /* <-- MUDANÇA: de 3 para 2 */
}
.smoke-element {
  position: absolute;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.35) 0%, rgba(255, 255, 255, 0) 70%); /* Fumaça MAIS densa */
  border-radius: 50%;
  filter: blur(70px); /* Mais blur */
  animation: smoke-drift 25s linear infinite; 
  opacity: 0; 
}
.smoke-element:nth-child(1) { top: 5%; left: -20%; width: 400px; height: 400px; animation-delay: 0s; }
.smoke-element:nth-child(2) { top: 30%; left: -5%; width: 500px; height: 500px; animation-delay: 7s; }
.smoke-element:nth-child(3) { top: 15%; right: -20%; width: 450px; height: 450px; animation-delay: 14s; }
.smoke-element:nth-child(4) { bottom: 10%; right: -5%; width: 550px; height: 550px; animation-delay: 21s; }

@keyframes smoke-drift {
  0% { transform: translateX(-150%); opacity: 0; }
  25% { transform: translateX(0%); opacity: 0.8; } /* Opacidade MAIS ALTA */
  50% { transform: translateX(150%); opacity: 0; }
  100% { transform: translateX(-150%); opacity: 0; }
}

/* ONDAS LATERAIS DISTORCIDAS (Sua nova ideia, mais proeminentes) */
.side-wave {
  position: absolute;
  top: 20%; /* Começa um pouco mais acima */
  height: 60%; /* Mais altas */
  width: 120px; /* Mais largas */
  background: linear-gradient(to bottom, 
    transparent, 
    rgba(255, 165, 0, 0.15) 20%, /* Laranja mais visível */
    rgba(255, 165, 0, 0.3) 50%,  /* Laranja MAIS visível no centro */
    rgba(255, 165, 0, 0.15) 80%,
    transparent);
  opacity: 0.6; /* MAIS visível */
  filter: blur(20px); /* Mais blur */
  animation: side-wave-pulse 4s ease-in-out infinite alternate; /* Animação mais suave */
  pointer-events: none;
  z-index: 0; /* <-- OK: Fica bem no fundo */
}
.side-wave.left { left: 0; }
.side-wave.right { right: 0; animation-delay: 0.8s; } /* Pequeno delay */

@keyframes side-wave-pulse {
  0% { transform: scaleY(0.7) translateX(0px); opacity: 0.4; } /* Ligeiro movimento lateral */
  50% { transform: scaleY(1.1) translateX(10px); opacity: 0.7; }
  100% { transform: scaleY(0.7) translateX(0px); opacity: 0.4; }
}

/* Focos de Luz (Movings) */
/* As luzes ficam atrás da fumaça para um efeito mais realista */
.spotlight {
  position: absolute;
  top: -10%; 
  width: 150px; 
  height: 150%; 
  background: linear-gradient(to bottom, 
    rgba(255, 165, 0, 0.25) 0%, 
    rgba(255, 165, 0, 0) 70%); 
  pointer-events: none;
  z-index: 1; /* <-- OK: Atrás da fumaça (z-index 2) */
  filter: blur(20px);
  transform-origin: top center; 
  animation: light-pulse-base 5s infinite alternate; 
}
.spotlight.left-1 { left: 10%; transform: rotate(-40deg); animation-delay: 0s; }
.spotlight.left-2 { left: 30%; transform: rotate(-20deg); animation-delay: 1s; }
.spotlight.center { left: 50%; transform: translateX(-50%) rotate(0deg); animation-delay: 0.5s; }
.spotlight.right-1 { right: 10%; transform: rotate(40deg); animation-delay: 0.2s; }
.spotlight.right-2 { right: 30%; transform: rotate(20deg); animation-delay: 1.2s; }

@keyframes light-pulse-base {
  0% { opacity: 0.6; }
  100% { opacity: 1.0; } 
}
@media (max-width: 768px) {
  .spotlight {
    width: 100px; 
    filter: blur(15px);
  }
  .spotlight.left-1 { left: -5%; transform: rotate(-45deg); }
  .spotlight.left-2 { left: 20%; transform: rotate(-25deg); }
  .spotlight.center { left: 50%; transform: translateX(-50%) rotate(0deg); }
  .spotlight.right-1 { right: -5%; transform: rotate(45deg); }
  .spotlight.right-2 { right: 20%; transform: rotate(25deg); }
}

